.header{
    width: 100%;
    background: lightcoral;
    font-size: 30px;
    text-align: center;
}
.container-drag{
    display: flex;
    justify-content: space-between;
}
.wip,.droppable{
    width: 50%;
    height: 100vh;
    background: lightgreen;
}
.wip{
    border-right: 5px solid white;
}
.task-header{
  display: inline-block;
  font-size: 20px;
  background: black;
  color: white;
  width: 100%;
  text-align:center;
  padding: 20px;
  box-sizing: border-box;
}

.item{
    box-sizing: border-box;
    padding :20px;
    font-size:25px;

}
.additem{
    width: 100%;
    display: flex;
    margin:0px 0px 10px 0px;
}
.additem input{
    display: inline-block;
    width: 100%;
    font-size: 20px;
    padding: 5px;
}
.additem button{
    display: inline-block;
    font-size: 20px;
    width: 30%;
    outline: none;
    background: greenyellow;
    padding: 5px;
}